body {
    font-family: Google Sans;
    color: #212121;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

.navbar {
    transition: box-shadow .2s ease-in-out;
}

.navbar.has-shadow {
    box-shadow: 0 .125rem .3125rem rgba(0,0,0,.26);
}

.loader-wrapper {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #ffffff;
    z-index: 999999;
}

.pageloader {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: url('../assets/Google Dots by VRG.gif') no-repeat center center;
}

footer a {
    color: #212121;
    text-decoration: none;
}